// 引入tabbar
import tabbar from"@/components/common-tabbar/common-tabbar";
import commonHeader from"@/components/common-header/common-header"
export default {
	props: {
		//最大长度 值为4或者6
		maxlength: {
			type: Number,
			default: 6
		},
		//是否是密码
		isPwd: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			headerTitl:'结算',
			checked:false,
			payMaskHide:true,
			val:'',//输入框的值
			codeIndex: 1, //下标
			codeArr: [],
			payPwdMaskHide:true,
			// 支付方式
			payMode:'',
			// 支付自动聚焦
			focusStata:false,
			
			// 优惠弹窗
			yhqTetx:'暂无优惠券',
			setCouponHide:true,
			redpackgeList:[
				{id:"01",price:5,title:"5元剪发券",date:"2019-12-31"},
				{id:"02",price:10,title:"5元剪发券",date:"2019-12-31"},
				{id:"03",price:5,title:"5元剪发券",date:"2019-12-31"},
				{id:"04",price:99,title:"5元剪发券",date:"2019-12-31"},
				{id:"05",price:99,title:"5元剪发券",date:"2019-12-31"}
			],
			checkedradio:false
		}
	},
	methods: {
		remarks(){
			console.log(111)
		},
		// 前往地址
		goAddress(){
			uni.navigateTo({
				url:'../myAddress/myAddress'
			})
		},
		// 提交订单
		submitOrder(){
			this.payMaskHide = false;
		},
		// 获取输入的值
		//取值
		getVal(e) {
			let { value } = e.detail;
			this.val=value;
			let arr = value.split('');
			this.codeIndex = arr.length + 1;
			this.codeArr = arr;
			console.log(this.val)
			if(this.val==888888){
				uni.showLoading({
				    title: '支付中...',
					mask:true
				});
				setTimeout( ()=> {
					uni.showToast({
						title:'支付成功',
						success:()=> {
							  uni.hideLoading();
						}
					})
					uni.reLaunch({
						url:'../index/index'
					})
				}, 2000);
			}
		},
		//清除验证码或者密码
		clear(){
			this.codeIndex=1;
			this.codeArr=[];
			this.val="";
		},
		// 支付
		pay(index){
			if(index===0){
				this.payMode = '余额支付';
			}
			if(index===1){
				this.payMode = '微信支付';
			}
			if(index===2){
				this.payMode = '支付宝支付';
			}
			if(index===3){
				this.payMode = '银行卡支付';
			}
			// 隐藏当前支付方式选择
			this.payMaskHide = true;
			// 显示密码输入
			this.payPwdMaskHide = false;
		},
		// 返回支付选择
		backPay(){
			// 显示支付方式选择
			this.payMaskHide = false;
			// 隐藏密码输入
			this.payPwdMaskHide = true;
		},
		// 立即使用优惠券
		ljsy(item){
			if(item){
				this.yhqTetx = item.title;
				this.checkedradio = false;
				this.setCouponHide = true;
			}else{
				this.yhqTetx = '暂无优惠券';
				// this.setCouponHide = true;
			}
		}
	},
	components:{
		commonHeader,
		tabbar
	}
}
